import { Component, OnInit, Input, OnDestroy, Inject, EventEmitter } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/operators';
import { Subscription } from 'rxjs';

import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { Employee } from 'src/app/staff/services/employees/employee';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { NotificationsService } from 'src/app/shared/services/notifications/notifications.service';

@Component({
  selector: 'app-profile-page',
  templateUrl: './profile-page.component.html',
  styleUrls: ['./profile-page.component.scss']
})
export class ProfilePageComponent implements OnInit, OnDestroy {
  public employee: Employee;
  public modalIsOpen = false;
  public aviableThanksEmpty = false;
  public refreshThanks: EventEmitter<null> = new EventEmitter<null>();

  public userId: number;
  private employeeSubscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory,
    private sharepointService: SharepointService,
    private notificationsService: NotificationsService) {
    this.userId = this.sharepointService.getCurrentUserContext().userId;
  }

  public getEmployeeLink(employee: Employee) {
    return `${window.location.protocol}//${window.location.host}`
    + `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}`
    + `/PortalPages/home.aspx#/phonebook/${employee.id}`;
  }

  ngOnInit() {
    this.userId = this.route.snapshot.data.userId;
    this.employeeSubscription = this.route.data.pipe(map(data => data.employee))
      .subscribe(employee => this.employee = employee);
  }

  public toggleLike(): void {
    if (this.employee.likedByIds.indexOf(this.userId) >= 0) {
      this.employeesFactory.removeFavorite(this.employee.id).subscribe();
      this.employee.likedByIds = this.employee.likedByIds.filter(l => l !== this.userId);
    } else {
      this.employeesFactory.addFavorite(this.employee.id).subscribe();
      this.employee.likedByIds.push(this.userId);
    }
  }

  public openThanksModal() {
    if (!this.aviableThanksEmpty) {
      this.modalIsOpen = true;
    } else {
      this.notificationsService.warning(`На данный момент доступных благодарностей нет.
      Возможность оставить благодарность будет доступна в следующем месяце`);
    }
  }

  ngOnDestroy(): void {
    if (this.employeeSubscription != null) {
      this.employeeSubscription.unsubscribe();
    }
  }

  public closeThanksModal() {
    this.modalIsOpen = false;
  }
}
