import { Injectable, Inject } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { Observable } from 'rxjs';
import { Employee } from 'src/app/staff/services/employees/employee';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';

@Injectable()
export class EmployeeResolver implements Resolve<Employee> {
  constructor(@Inject('EmployeesFactory') private employeesFactory: EmployeesFactory) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Employee> {
    const id: number = route.params.id;
    return this.employeesFactory.getEmployeeById(id);
  }
}
