import {
  Component,
  OnInit,
  ElementRef,
  ViewChildren,
  ViewChild,
  QueryList,
  HostListener,
  AfterViewInit,
  EventEmitter,
  OnDestroy,
  Inject
} from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { map, debounceTime } from 'rxjs/operators';
import {
  ActivatedRoute
} from '@angular/router';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { SharepointService } from 'src/app/services/sharepoint/sharepoint.service';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';
import { Department } from 'src/app/staff/services/departments/department';
import { Employee } from 'src/app/staff/services/employees/employee';
import { NotificationsService } from 'src/app/shared/services/notifications/notifications.service';
@Component({
  selector: 'app-phonebook-page',
  templateUrl: './phonebook-page.component.html',
  styleUrls: ['./phonebook-page.component.scss']
})
export class PhonebookPageComponent
  implements OnInit, AfterViewInit, OnDestroy {
  @ViewChildren('employeeCardElement', { read: ElementRef })
  employeeCardElements: QueryList<ElementRef>;

  @ViewChild('searchRef') searchRef: ElementRef;

  public departments: Department[];
  public department: Department = null;
  public myDepartmentId: number = null;
  public page: IListItemsPage<Employee>;
  public hasNext = false;
  public employees: Employee[] = [];
  public userId: number = null;
  public currentEmployee: Employee = null;
  public subscription: Subscription = null;
  public filter: string = null;
  public isFavorite = false;
  public departmentId: number = null;
  public employee: Employee = null;
  public modalIsOpen = false;
  public aviableThanksEmpty = false;

  constructor(
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory,
    private route: ActivatedRoute,
    private sharepointService: SharepointService,
    private notificationsService: NotificationsService
  ) {
    this.userId = this.sharepointService.getCurrentUserContext().userId;
  }

  ngAfterViewInit(): void {
    this.subscription = fromEvent(this.searchRef.nativeElement, 'keyup')
      .pipe(
        map((e: any) => e.target.value),
        debounceTime(1000)
      )
      .subscribe((filter: string) => {
        this.filter = filter;
        this.searchEmployee();
      });
  }

  ngOnInit() {
    this.departmentId = Number(this.route.snapshot.params.id);
    this.userId = this.route.snapshot.data.userId;
    this.departments = this.route.snapshot.data.departments;
    this.currentEmployee = this.route.snapshot.data.currentEmployee;
    this.createPage(this.route.snapshot.data.employees);
  }

  ngOnDestroy(): void {
    if (this.subscription != null) {
      this.subscription.unsubscribe();
    }
  }

  @HostListener('document:click', ['$event'])
  clickOutSide(el) {
    this.employeeCardElements.toArray().forEach(element => {
      if (!element.nativeElement.contains(el.target)) {
        element.nativeElement.className = 'employee';
      }
    });
  }

  toggleCard(employeeCardElement: HTMLElement) {
    const el = employeeCardElement;
    if (el.classList.contains('is-expanded')) {
      el.className = 'employee';
    } else {
      this.employeeCardElements.toArray().forEach(element => {
        element.nativeElement.className = 'employee';
      });
      el.className += ' is-expanded';
    }
  }

  public getAllEmployees(event?): void {
    this.myDepartmentId = null;
    this.isFavorite = false;
    this.searchEmployee();
  }

  public getLikedEmployees(event?): void {
    this.myDepartmentId = null;
    this.isFavorite = true;
    this.searchEmployee();
  }

  public getMyEmployees(event?): void {
    this.myDepartmentId = this.currentEmployee.lookupDepartmentId;
    this.isFavorite = false;
    this.searchEmployee();
  }

  public getEmergencyEmployees(event?): void {
    // this.searchRef.nativeElement.value = null;
    // this.employeesService
    //   .getItemsPaged({ filter: `slIsEmergency eq 1` })
    //   .subscribe(page => this.createPage(page));
  }

  public getEmployeesByDepartment(department): void {
    console.log('department ', department);
    this.department = department;
    this.searchEmployee();
  }

  public toggleLike(employee: Employee): void {
    if (this.isLikedEmployee(employee)) {
      this.employeesFactory.removeFavorite(employee.id).subscribe();
      employee.likedByIds = employee.likedByIds.filter(l => l !== this.userId);
    } else {
      this.employeesFactory.addFavorite(employee.id).subscribe();
      employee.likedByIds.push(this.userId);
    }
  }

  public isLikedEmployee(employee: Employee): boolean {
    const isLiked =
      (employee.likedByIds && employee.likedByIds.indexOf(this.userId) >= 0) ||
      false;
    return isLiked;
  }

  public getNextEmployees(): void {
    this.page.getNext().subscribe(page => this.addPage(page));
  }

  public searchEmployee(): void {
    const value = this.filter;
    const departmentId: number =
      this.myDepartmentId || (this.department && this.department.id);
    this.employeesFactory
      .search(value, departmentId, this.isFavorite)
      .subscribe(page => this.createPage(page));
  }

  private createPage(page: IListItemsPage<Employee>): void {
    this.page = page;
    this.hasNext = page.hasNext;
    this.employees = page.items;
  }

  private addPage(page: IListItemsPage<Employee>): void {
    this.page = page;
    this.hasNext = page.hasNext;
    this.employees = this.employees.concat(page.items);
  }

  public onKeypress(event) {
    if (event.key === 'Enter') {
      event.preventDefault();
      return false;
    }
  }

  public getEmployeeLink(employee: Employee) {
    return (
      `${window.location.protocol}//${window.location.host}` +
      `${this.sharepointService.getCurrentUserContext().webServerRelativeUrl}` +
      `/PortalPages/home.aspx#/phonebook/${employee.id}`
    );
  }

  public thanksTo(employee: Employee) {
    if (!this.aviableThanksEmpty) {
      this.employee = employee;
      this.modalIsOpen = true;
    } else {
      this.notificationsService.warning(`На данный момент доступных благодарностей нет.
      Возможность оставить благодарность будет доступна в следующем месяце`);
    }
  }

  public closeThanksModal() {
    this.modalIsOpen = false;
  }
}
