import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PhonebookPageComponent } from './components/phonebook-page/phonebook-page.component';
import { ProfilePageComponent } from './components/profile-page/profile-page.component';

import { CurrentUserIdResolver } from 'src/app/services/security/current-user-id.resolver';
import { EmployeesResolver } from './services/employees/employees.resolver';
import { EmployeesDepartmentResolver } from './services/employees/employees-department.resolver';
import { CurrentEmployeeResolver } from './services/employees/current-employee.resolver';
import { DepartmentsResolver } from './services/departments/departments.resolver';
import { EmployeeResolver } from './services/employees/employee.resolver';
// import { MsalGuard } from '@azure/msal-angular';

const routes: Routes = [
  {
    path: '',
    component: PhonebookPageComponent,
    // canActivate : [MsalGuard],
    resolve: {
      employees: EmployeesResolver,
      departments: DepartmentsResolver,
      currentEmployee: CurrentEmployeeResolver,
      userId: CurrentUserIdResolver
    },
    data: { animation: 'fadeAnimation', title: 'Телефонный справочник' }
  },
  {
    path: ':id',
    component: ProfilePageComponent,
    // canActivate : [MsalGuard],
    resolve: { employee: EmployeeResolver, userId: CurrentUserIdResolver },
    data: { animation: 'fadeAnimation', title: 'Профиль пользователя' }
  },
  {
    path: 'department/:id',
    component: PhonebookPageComponent,
    // canActivate : [MsalGuard],
    resolve: {
      employees: EmployeesDepartmentResolver,
      departments: DepartmentsResolver,
      currentEmployee: CurrentEmployeeResolver,
      userId: CurrentUserIdResolver
    },
    data: { animation: 'fadeAnimation', title: 'Телефонный справочник' }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  providers: [
    EmployeesResolver,
    EmployeesDepartmentResolver,
    DepartmentsResolver,
    CurrentEmployeeResolver,
    CurrentUserIdResolver,
    EmployeeResolver
  ],
  exports: [RouterModule]
})
export class PhonebookRoutingModule {}
